import styled from "styled-components";

const StyledLogin = styled.div`
  min-height: 100vh;
  background-color: var(--color-background);
  display: flex;
  align-items: center;
  padding: 0 1rem;
  position: relative;

  .loader-container {
    position: absolute;
    left: 50%;
    top: 12%;
    transform: translate(-50%, 0%);
  }

  .intro {
    padding: 2rem 0;
    &__text {
      font-size: 1.8rem;
      text-align: center;
    }
  }

  form {
    .input-field {
      border-radius: 10rem;
      font-size: 1.6rem;
      padding: 1.4rem;
      margin: 1rem 0;

      &:focus {
        border-color: var(--color-primary);
        box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.075) inset,
          0px 0px 4px var(--color-shadow-primary);
      }
    }

    .terms-container {
      display: flex;
      align-items: center;

      p.toggle-terms {
        padding: 0 1.4rem;
        margin: 0;
        font-size: 1.6rem;
        text-decoration: underline;
        text-align: center;

        &:hover {
          cursor: pointer;
        }
      }

      .check-box {
        font-size: 1.4rem;
        padding: 1rem 0rem 1rem 2.5rem;
        display: flex;
        align-items: center;
        label {
          padding: 0 1rem;
        }
      }
    }

    button {
      width: 100%;
      font-size: 1.6rem;
      padding: 1rem;
      margin: 1rem 0;
      font-weight: bold;
    }

    p.error {
      color: red;
      font-size: 1.6rem;
      text-align: center;
      margin: 0;
      &::before {
        content: "*";
      }
    }
  }
`;

export default StyledLogin;
