import React from "react";
import StyledNav from "./StyledNav";
import Logo from "../../images/logo.png";
import { Container } from "react-bootstrap";

function Nav() {
  return (
    <StyledNav>
      <Container>
        <img src={Logo} alt="Immunity Health" className="logo" />
      </Container>
    </StyledNav>
  );
}

export default Nav;
