export const normalizeDetails = scatter => {
  const data = scatter.jsonData;

  let intro = "";
  let objectives = [];
  let requirements = [];
  let partner = "";
  let types = [];
  let outro = "";
  let website = "";
  let img_url = "";

  data.forEach(datum => {
    if (datum.type === "objective") objectives.push(datum.value);
    if (datum.type === "requirement") requirements.push(datum.value);
    if (datum.type === "location") partner = datum.value;
    if (datum.type === "type") types.push(datum.value);
    if (datum.type === "content" && datum.number === "1") intro = datum.value;
    if (datum.type === "content" && datum.number === "2") outro = datum.value;
    if (datum.type === "website") website = datum.value;
    if (datum.type === "img_url") img_url = datum.value;
  });

  return {
    intro,
    objectives,
    partner,
    types,
    requirements,
    outro,
    website,
    img_url
  };
};
