import styled from "styled-components";

const StyledDetails = styled.div`
  padding: 0 2rem;
  padding-top: 6rem;
  background-color: var(--color-background);

  .header {
    padding: 1rem 0rem 3rem 0rem;
    display: flex;
    align-items: center;
    a {
      display: flex;
      align-items: center;

      ion-icon[name="arrow-back"] {
        color: var(--color-secondary);
        font-size: 2.4rem;
      }
    }
    &__title {
      font-size: 2rem;
      margin: 0;
      padding: 0 0.5rem;
    }
  }

  /* BANNER */
  .banner {
    height: 50vh;
    background-color: red;
    border-top-left-radius: 3rem;
    border-top-right-radius: 3rem;
  }

  /* LOADER */
  .loader {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  /* INTRO */
  .intro {
    &__heading {
      font-size: 2rem;
      font-weight: 700;
      padding: 1rem 0;
      margin: 0;
    }

    &__content {
      font-size: 1.4rem;
      line-height: 1.8rem;
    }

    &__list {
      padding: 1rem 0rem 1rem 3rem;

      &--item {
        font-size: 1.4rem;
        margin: 1rem 0;
      }
    }
  }

  /* FEATURE */
  .feature {
    &__subheading {
      font-size: 1.6rem;
      font-weight: 700;
      span {
        color: var(--color-primary);
        a {
          color: inherit;
        }
      }
    }

    &__heading {
      margin: 0;
      padding: 1rem 0;
      font-size: 1.6rem;
      font-weight: 700;
    }

    &__content {
      display: flex;

      &--item {
        border: 1px solid black;
        font-size: 1.4rem;
        padding: 0.8rem 2rem;
      }

      &--list {
        list-style: none;

        li {
          font-size: 1.4rem;

          &::before {
            content: "â€¢ ";
            color: red; /* or whatever color you prefer */
          }
        }
      }
    }
  }

  /* OUTRO */
  .outro {
    padding-top: 1rem;
    padding-bottom: 10rem;
    &__text {
      font-size: 1.4rem;
      line-height: 1.8rem;
    }
  }

  /* FOOTER */
  .footer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 2rem;

    background-color: var(--color-background);
    button {
      font-size: 1.6rem;
      padding: 1rem 2rem;
      width: 100%;
    }
  }
`;

export default StyledDetails;
