import React, { useState, useEffect } from "react";
import StyledLogin from "./StyledLogin";
import { Container, Form, Button } from "react-bootstrap";
import axios from "axios";
import VerticalModal from "../../components/VerticalModal/VerticalModal";
import { withRouter } from "react-router-dom";
import Loader from "react-loader-spinner";

const DEV = true;

// APIs
const DEV_API = {
  fetch_terms: "https://uat.immunityhealth.me/api/terms-condition",
  confirm_terms: "https://uat.immunityhealth.me/api/terms-condition-save",
  fetch_otp: "https://uat.immunityhealth.me/api/login-mobile",
  verify_otp: "https://uat.immunityhealth.me/api/login-mobile-verify",
  register_user: "https://uat.immunityhealth.me/api/user-info"
};

const PROD_API = {
  fetch_terms: "http://13.126.71.226:3000/api/terms-condition",
  confirm_terms: "http://13.126.71.226:3000/api/terms-condition-save",
  fetch_otp: "http://13.126.71.226:3000/api/login-mobile",
  verify_otp: "http://13.126.71.226:3000/api/login-mobile-verify",
  register_user: "http://13.126.71.226:3000/api/user-info"
};

function Login({ history }) {
  //   Initial page states
  const [modalShow, setModalShow] = useState(false);
  const [isValidated, setIsValidated] = useState({
    number: false,
    otp: false
  });
  const [isLoading, setIsLoading] = useState(false);
  const [buttonText, setButtonText] = useState("Get OTP");
  const [terms, setTerms] = useState("");
  const [newUser, setNewUser] = useState(false);
  //   User states
  const [phoneNumber, setPhoneNumber] = useState("");
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [pincode, setPincode] = useState("");
  const [userOtp, setUserOtp] = useState("");
  const [termsAccepted, setTermsAccepted] = useState(false);
  const [error, setError] = useState("");

  //   Generate terms and services content
  const fetchTermsAndServices = async () => {
    setIsLoading(true);
    try {
      const response = await axios.post(
        DEV ? DEV_API.fetch_terms : PROD_API.fetch_terms,
        {
          type: 1
        }
      );
      setIsLoading(false);
      setTerms(response.data.jsonData.content);
    } catch (e) {
      setIsLoading(false);
    }
  };

  // Confirm save term and services
  const confirmTermsAndServices = async () => {
    setIsLoading(true);
    try {
      const response = await axios.post(
        DEV ? DEV_API.confirm_terms : PROD_API.confirm_terms,
        {
          mobile_number: +phoneNumber,
          device_id: 123456,
          type: 1,
          content_id: 1
        }
      );
      setIsLoading(false);
    } catch (e) {
      setIsLoading(false);
    }
  };

  //   Generate new OTP
  const generateNewOTP = async () => {
    setIsLoading(true);
    try {
      //uat.immunityhealth.me/api/login-mobile
      const response = await axios.post(
        DEV ? DEV_API.fetch_otp : PROD_API.fetch_otp,
        {
          mobile: +phoneNumber
        }
      );
      setIsValidated(prevState => ({ ...prevState, otp: true }));
      setButtonText("Verify");
      setIsLoading(false);
    } catch (e) {
      setIsLoading(false);
    }
  };

  // Verify OTP
  const verifyOTP = async () => {
    if (userOtp.length < 4) {
      setError("Please enter the 4-digit OTP");
      return;
    }
    setIsLoading(true);
    try {
      const response = await axios.post(
        DEV ? DEV_API.verify_otp : PROD_API.verify_otp,
        {
          mobile: +phoneNumber,
          otp: +userOtp
        }
      );
      if (response.data.status === "false") {
        setError("Incorrect OTP");
        setIsLoading(false);
        return;
      }
      setIsLoading(false);
      if (response.data.jsonData) {
        localStorage.setItem("token", response.data.jsonData.token);
        history.push("/details");
      } else {
        setNewUser(true);
        setButtonText("Submit");
      }
    } catch (e) {
      setIsLoading(false);
    }
  };

  // Register new user
  const registerNewUser = async () => {
    if (!name) {
      setError("Name is mandatory");
      return;
    }
    if (!email) {
      setError("Email is mandatory");
      return;
    }
    if (!email.includes("@") || !email.includes(".") || !email.length > 5) {
      setError("Please enter a valid email address");
      return;
    }
    if (pincode.length < 6) {
      setError("A six-digit Pin Code is mandatory");
      return;
    }
    if (!termsAccepted) {
      setError("Please accept terms and conditions");
      return;
    }

    confirmTermsAndServices();

    setIsLoading(true);
    localStorage.setItem("pincode", pincode);
    try {
      const response = await axios.post(
        DEV ? DEV_API.register_user : PROD_API.register_user,
        {
          mobile: +phoneNumber,
          name: name,
          email: email
        }
      );
      setIsLoading(false);
      if (response.data.jsonData.token) {
        localStorage.setItem("token", response.data.jsonData.token);
        history.push("/details");
      }
      setError("Invalid data");
    } catch (e) {
      setIsLoading(false);
    }
  };

  //   Button handler for calling required functions
  const handleButton = () => {
    if (buttonText === "Get OTP") generateNewOTP();
    if (buttonText === "Verify") verifyOTP();
    if (buttonText === "Submit") registerNewUser();
  };

  //   ERROR HANDLER
  useEffect(() => {
    isNaN(phoneNumber) ? setError("Invalid mobile number") : setError("");
    phoneNumber.length === 10 &&
      !error &&
      setIsValidated(prevState => ({ ...prevState, number: true }));
  }, [phoneNumber]);

  // ERROR HANDLER for New Users
  useEffect(() => {
    if (error === "Name is mandatory") {
      name.length > 1 && setError("");
    }
    if (error === "Email is mandatory") {
      email.length > 1 && setError("");
    }
    if (error === "Please enter a valid email address") {
      email.includes("@") &&
        email.includes(".") &&
        email.length > 5 &&
        setError("");
    }
    if (error === "A six-digit Pin Code is mandatory") {
      pincode.length === 6 && setError("");
    }
    if (error === "Please accept terms and conditions") {
      termsAccepted && setError("");
    }
    if (error === "Please enter the 4-digit OTP") {
      userOtp.length === 4 && setError("");
    }
    if (error === "Incorrect OTP") {
      userOtp.length < 4 && setError("");
    }
  }, [error, name, email, pincode, termsAccepted, userOtp]);

  // ON LOAD
  useEffect(() => {
    fetchTermsAndServices();
  }, []);

  return (
    <StyledLogin>
      {/* TERMS MODAL */}
      <VerticalModal show={modalShow} onHide={() => setModalShow(false)}>
        {terms}
      </VerticalModal>

      {/* LOADER */}
      <div className="loader-container">
        {isLoading && (
          <Loader
            type="Puff"
            color="var(--color-primary)"
            height={80}
            width={80}
          />
        )}
      </div>

      <Container>
        <div className="intro">
          <p className="intro__text">
            Hello and Welcome! Log-in or Sign-up quickly using your mobile
            number.
          </p>
        </div>
        {/* FORM */}
        <Form onSubmit={e => e.preventDefault()}>
          <Form.Control
            type="text"
            placeholder="Mobile Number"
            className="input-field"
            onChange={e => setPhoneNumber(e.target.value)}
            maxLength="10"
          />
          {isValidated.otp && (
            <Form.Control
              type="text"
              placeholder="Enter OTP"
              className="input-field"
              onChange={e => setUserOtp(e.target.value)}
              maxLength="4"
            />
          )}
          {newUser && (
            <>
              <Form.Control
                type="text"
                placeholder="Your Name"
                className="input-field"
                onChange={e => setName(e.target.value)}
                required
              />
              <Form.Control
                type="email"
                placeholder="Your Email"
                className="input-field"
                onChange={e => setEmail(e.target.value)}
                required
              />
              <Form.Control
                type="text"
                placeholder="Pin Code"
                className="input-field"
                onChange={e => setPincode(e.target.value)}
                maxLength="6"
                required
              />

              <Form.Group
                controlId="formBasicCheckbox"
                className="terms-container"
              >
                <Form.Check
                  type="checkbox"
                  onChange={e => setTermsAccepted(e.target.checked)}
                  className="check-box"
                />
                <p onClick={() => setModalShow(true)} className="toggle-terms">
                  Terms and Conditions
                </p>
              </Form.Group>
            </>
          )}

          {error && <p className="error">{error}</p>}
          {!error && phoneNumber.length === 10 && (
            <Button className="primary" onClick={handleButton}>
              {buttonText}
            </Button>
          )}
        </Form>
      </Container>
    </StyledLogin>
  );
}

export default withRouter(Login);
