import React, { useEffect, useState } from "react";
import StyledDetails from "./StyledDetails";
import { Link, withRouter } from "react-router-dom";
import axios from "axios";
import { normalizeDetails } from "../../helpers/normalize";
import Loader from "react-loader-spinner";

function Details({ history }) {
  const [isLoading, setIsLoading] = useState(false);
  const [data, setData] = useState({});
  // GENERATE DATA
  const fetchData = async (bodyParameters, configuration) => {
    setIsLoading(true);
    try {
      const response = await axios.post(
        "https://uat.immunityhealth.me/api/dynamicScreenDetail",
        bodyParameters,
        configuration
      );
      setData(normalizeDetails(response.data));
      setIsLoading(false);
    } catch (e) {
      if (e.message === "Request failed with status code 401") {
        setIsLoading(false);
        history.push("/");
      }
    }
  };

  useEffect(() => {
    let configuration = {
      headers: {
        "Content-Type": "application/json;charset=UTF-8",
        "Access-Control-Allow-Origin": "*",
        Authorization: `Bearer ${localStorage.getItem("token")}`
      }
    };

    // SET PARAMETERS
    const bodyParameters = {
      product_category_id: 4,
      screen_number: 5
    };
    // FETCH DATA
    fetchData(bodyParameters, configuration);
  }, []);
  return (
    <StyledDetails>
      {/* HEADER */}
      <div className="header">
        <p className="header__title">Breathe</p>
      </div>

      {/* BANNER */}
      {data.img_url && (
        <div
          className="banner"
          style={{
            background: `url(${data.img_url}) no-repeat`,
            backgroundSize: "cover"
          }}
        ></div>
      )}

      {isLoading && (
        <Loader
          type="ThreeDots"
          color="var(--color-primary)"
          height={150}
          width={150}
          className="loader"
        />
      )}

      {!isLoading && (
        <>
          {/* INTRO */}
          <div className="intro">
            <p className="intro__heading">What's covered?</p>
            <p className="intro__content">{data.intro}</p>
            <ul className="intro__list">
              {data.objectives &&
                data.objectives.map((objective, i) => (
                  <li className="intro__list--item" key={i}>
                    {objective}
                  </li>
                ))}
            </ul>
          </div>

          {/* FEATURES */}
          <div className="features">
            <div className="feature">
              <p className="feature__subheading">
                Partner:{" "}
                <span>
                  <a href={data.website}>{data.partner}</a>
                </span>
              </p>
            </div>

            <div className="feature">
              <p className="feature__heading">Type</p>
              <div className="feature__content">
                {data.types &&
                  data.types.map((type, i) => (
                    <p className="feature__content--item" key={i}>
                      {type}
                    </p>
                  ))}
              </div>
            </div>

            <div className="feature">
              <p className="feature__heading">Location</p>
              <div className="feature__content">
                <p className="feature__content--item">Home</p>
              </div>
            </div>

            <div className="feature">
              <p className="feature__heading">Requirement:</p>
              <div className="feature__content">
                <ul className="feature__content--list">
                  {data.requirements &&
                    data.requirements.map((requirement, i) => (
                      <li key={i}>{requirement}</li>
                    ))}
                </ul>
              </div>
            </div>
          </div>

          {/* OUTRO */}
          <div className="outro">
            <p className="outro__text">{data.outro}</p>
          </div>

          {/* FOOTER :: BUTTON */}
          <div className="footer">
            <Link to="/appointment">
              <button className="primary">Buy Now</button>
            </Link>
          </div>
        </>
      )}
    </StyledDetails>
  );
}

export default withRouter(Details);
