import styled from "styled-components";

const StyledAppointment = styled.div`
  padding: 0 2rem;
  padding-top: 6rem;
  background-color: var(--color-background);
  min-height: 120vh;

  .header {
    padding: 1rem 0rem 3rem 0rem;
    display: flex;
    align-items: center;

    a {
      display: flex;
      align-items: center;

      ion-icon[name="arrow-back"] {
        color: var(--color-secondary);
        font-size: 2.4rem;
      }
    }

    &__title {
      font-size: 2rem;
      margin: 0;
      padding: 0 0.5rem;
    }
  }

  /* BANNER */
  .banner {
    height: 50vh;
    background-color: red;

    border-top-left-radius: 3rem;
    border-top-right-radius: 3rem;
  }

  /* CONTENT */
  .content {
    &__heading {
      font-size: 2rem;
      font-weight: 700;
      padding: 1rem 0;
      margin: 0;
    }
  }

  /* DATE PICKER */
  .date-picker {
    &__heading {
      font-size: 1.6rem;
      font-weight: 700;
    }

    &__container {
      display: flex;
      overflow-x: auto;
      white-space: nowrap;

      &::-webkit-scrollbar {
        display: none;
      }
    }

    &__date {
      display: block;
      position: relative;
      cursor: pointer;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      margin-right: 3rem;
      text-align: center;

      input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 5rem;
        width: 4.5rem;
        top: 10%;
        left: 0;
      }
      input[type="radio"]:checked + span p:last-child {
        background-color: var(--color-primary);
        color: var(--color-background);
      }
      input[type="radio"]:checked + span p:first-child {
        color: var(--color-primary);
      }

      &--day {
        font-weight: 600;
        font-size: 1.4rem;
      }
      &--date {
        font-size: 2rem;
        padding: 0.5rem;
        width: 4rem;
        border-radius: 100%;
      }
    }
  }

  /* TIME PICKER */
  .time-picker {
    padding-bottom: 10rem;
    &__heading {
      font-size: 1.6rem;
      font-weight: 700;
      padding-top: 1.5rem;
    }

    &__container {
      padding-bottom: 2rem;
    }

    &__time {
      display: flex;
      justify-content: space-between;
      align-items: center;

      &--time {
        font-size: 1.8rem;
        margin: 0;
      }
      input {
        height: 5rem;
        width: 2rem;
        order: 2;
      }
      input[type="radio"]:checked ~ p {
        color: red;
      }
    }
  }

  /* FOOTER */
  .footer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 2rem;

    background-color: var(--color-background);
    button {
      font-size: 1.6rem;
      padding: 1rem 2rem;
      width: 100%;
    }
  }

  /* LOADER */
  .loader {
    text-align: center;
  }

  /* ERROR */
  .error {
    text-align: center;
    color: red;
    font-size: 1.6rem;
    font-weight: 700;
    &::before {
      content: "*";
    }
  }
`;

export default StyledAppointment;
